.toggle {
  position: absolute;
  top: 10px;
  right: 10px;
  cursor: pointer;

  @media screen and (max-width: 767px) {
    display: none;
  }

  &__track {
    width: 42px;
    height: 22px;
    border-radius: 21px;

    display: flex;

    box-sizing: border-box;
    background-color: #ffffff;
    border: 1px solid #000000;

    &--checked {
      background-color: #000000;
      border: 1px solid #ffffff;
    }

    &__finish {
      display: flex;
      align-items: center;
      justify-content: center;

      width: 20px;
      height: 20px;
    }

    &__start {
      display: flex;
      align-items: center;
      justify-content: center;

      width: 20px;
      height: 20px;
    }
  }

  &__sun {
    display: block;
  }

  &__moon {
    display: block;
  }

  &__control {
    position: absolute;
    top: 1px;
    left: 1px;
    width: 20px;
    height: 20px;
    border: 1px solid #000000;
    border-radius: 50%;
    background-color: #ffffff;

    &--checked {
      transform: translate(20px);
      background-color: #000000;
      border: 1px solid #ffffff;
    }

    &:hover,
    &:focus {
      box-shadow: 0 0 2px 2px #ff6b08;
    }
  }
}

.toggle-control--checked {
  transform: translate(20px);
  background-color: #000000;
  border: 1px solid #ffffff;
}

.body--dark-theme {
  background-color: #0f0f0f;
  color: #ffffff;
}

.container-footer.container-footer--dark-theme {
  background-color: #181818;
  color: #ffffff;
}

.team-modal-link.team-modal-link--dark-theme {
  color: #ffffff;
  text-decoration: underline;
}

.underline.underline--dark-theme {
  color: #ffffff;
}
